.movie-details {
    background-color: $bg;
    z-index: 200;
    height: calc(100vh - 145px);
    width: 100vw;
    position: fixed;
    padding: 50px 100px;
    // overflow: hidden;

    display: flex;
    gap: 100px;

    .info {
        flex-basis: 50%;
        height: 100%;

        display: flex;
        flex-direction: column;
        // justify-content: space-between;
        // gap: 25px;

        h2.title {
            font-size: 25px;
            color: $white;
            margin-bottom: 7px;
        }

        span.length {
            font-weight: 300;
            //     margin-bottom: 25px;
        }

        .catagories {
            // flex-grow: 1;
            display: flex;
            flex-wrap: wrap;
            // align-items: flex-start;
            gap: 10px;
            margin: 35px 0;

            & > * {
                border: 1px solid $tool;
                border-radius: 100px;
                padding: 5px 10px;
                // color: $white;
                // font-weight: 100;
            }
        }

        .des {
            // flex-grow: 1;
            display: flex;
            flex-direction: column;
            gap: 25px;
            p.full-des {
                // margin-bottom: 15px;
                font-size: 18px;
                // color: $white;
            }

            .credits {
                p.credit {
                    // font-size: 14px;
                }
            }
        }

        .btns-container {
            display: flex;
            gap: 25px;

            margin-top: 50px;

            button {
                padding: 10px 15px;
                font-size: 16px;
                border-radius: 7px;
            }

            .watch {
            }

            .back {
                background: transparent;
                border: 1px solid $txt;
                color: $txt;
            }
        }
    }

    .movie-container {
        flex-basis: 50%;
        overflow: hidden;
        align-self: flex-start;
        position: relative;
        height: fit-content;

        .img-container {
            img {
                width: 100%;
                aspect-ratio: 16/9;
                object-fit: cover;
            }
        }

        .video-container {
            // width: 100%;
            // height: 100%;
            iframe {
            }
        }
    }
}
