.movies-main {
    flex-grow: 1;
    height: calc(100vh - 125px);
    // width: 100vw;
    // overflow-x: hidden;
    overflow-y: auto;
    display: grid;
    grid-template-columns: 200px 1fr;

    position: relative;

    & > * {
        padding: 25px 0;
    }

    .filter {
        grid-column: 1;
        grid-row: 1;
        position: fixed;
    }

    .movie-list {
        grid-column: 2;
        grid-row: 1;

        display: grid;
        grid-template-columns: repeat(auto-fill, 250px);
        gap: 30px;
        justify-content: center;

        & > * {
            justify-self: center;
        }
    }
}
