.movie-preview {
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-bottom: 75px;
    cursor: pointer;
    // justify-content: center;
    transform: scale(100%);
    transition: transform ease-in 0.2s;
    width: 100%;

    position: relative;

    &:hover {
        // transform: scale(107%);
        // transition: transform ease-in 0.2s;

        p {
            display: block;
            color: $txt;
            position: absolute;
            top: 100%;
        }
    }

    .img-container {
        height: 150px;

        img {
            width: 100%;
            object-fit: cover;
        }
    }

    h3 {
        color: $white;
        font-size: 18px;
        font-weight: 400;
        text-align: center;
        margin-bottom: 5px;
    }

    p {
        display: none;
        width: 100%;
        text-align: center;
        // z-index: 100;
    }
}
